"use client"

import { useState } from "react"
import { SellerSidebar } from "@/components/seller/sidebar"
import { SellerHeader } from "@/components/seller/header"
import { TrendingUp, TrendingDown, DollarSign, ShoppingCart, Users, Package, Eye, Calendar, Download, Filter } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

interface AnalyticsData {
  revenue: number
  orders: number
  customers: number
  products: number
  views: number
  conversionRate: number
  avgOrderValue: number
  growth: {
    revenue: number
    orders: number
    customers: number
    views: number
  }
}

interface TopProduct {
  id: number
  name: string
  sales: number
  revenue: number
  views: number
  conversionRate: number
}

export default function SellerAnalyticsPage() {
  const [timeRange, setTimeRange] = useState("7days")
  const [selectedMetric, setSelectedMetric] = useState("revenue")

  const analytics: AnalyticsData = {
    revenue: 45678,
    orders: 234,
    customers: 189,
    products: 45,
    views: 12567,
    conversionRate: 1.86,
    avgOrderValue: 195.12,
    growth: {
      revenue: 12.5,
      orders: 8.3,
      customers: 15.2,
      views: -5.4
    }
  }

  const topProducts: TopProduct[] = [
    {
      id: 1,
      name: "Wireless Headphones",
      sales: 67,
      revenue: 16733,
      views: 2340,
      conversionRate: 2.86
    },
    {
      id: 2,
      name: "USB-C Cable",
      sales: 89,
      revenue: 35511,
      views: 4567,
      conversionRate: 1.95
    },
    {
      id: 3,
      name: "Phone Case",
      sales: 34,
      revenue: 20366,
      views: 1234,
      conversionRate: 2.76
    },
    {
      id: 4,
      name: "Laptop Stand",
      sales: 23,
      revenue: 11500,
      views: 890,
      conversionRate: 2.58
    },
    {
      id: 5,
      name: "Wireless Mouse",
      sales: 21,
      revenue: 8400,
      views: 1123,
      conversionRate: 1.87
    }
  ]

  const recentOrders = [
    { id: "#12345", customer: "Sarah Johnson", amount: 2499, status: "Completed", time: "2 hours ago" },
    { id: "#12346", customer: "Mike Chen", amount: 399, status: "Processing", time: "3 hours ago" },
    { id: "#12347", customer: "Emily Davis", amount: 599, status: "Completed", time: "5 hours ago" },
    { id: "#12348", customer: "John Smith", amount: 1299, status: "Pending", time: "6 hours ago" }
  ]

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-IN', {
      style: 'currency',
      currency: 'INR',
      minimumFractionDigits: 0
    }).format(amount)
  }

  const getGrowthIcon = (growth: number) => {
    return growth >= 0 ? (
      <TrendingUp className="h-4 w-4 text-green-600" />
    ) : (
      <TrendingDown className="h-4 w-4 text-red-600" />
    )
  }

  const getGrowthColor = (growth: number) => {
    return growth >= 0 ? "text-green-600" : "text-red-600"
  }

  const MetricCard = ({ title, value, growth, icon, prefix = "", suffix = "" }: {
    title: string
    value: string | number
    growth: number
    icon: React.ReactNode
    prefix?: string
    suffix?: string
  }) => (
    <Card>
      <CardContent className="p-6">
        <div className="flex items-center justify-between">
          <div>
            <p className="text-sm font-medium text-muted-foreground">{title}</p>
            <p className="text-2xl font-bold mt-2">
              {prefix}{value}{suffix}
            </p>
            <div className="flex items-center gap-1 mt-2">
              {getGrowthIcon(growth)}
              <span className={`text-sm font-medium ${getGrowthColor(growth)}`}>
                {growth >= 0 ? "+" : ""}{growth}%
              </span>
              <span className="text-sm text-muted-foreground">vs last period</span>
            </div>
          </div>
          <div className="h-12 w-12 bg-primary/10 rounded-lg flex items-center justify-center text-primary">
            {icon}
          </div>
        </div>
      </CardContent>
    </Card>
  )

  return (
    <div className="flex bg-background">
      <SellerSidebar />

      <div className="flex-1 flex flex-col">
        <SellerHeader />

        <main className="flex-1 p-8">
          <div className="mb-8 flex justify-between items-center">
            <div>
              <h1 className="text-3xl font-bold mb-2">Analytics</h1>
              <p className="text-muted-foreground">Track your store performance and sales insights</p>
            </div>
            <div className="flex gap-4 items-center">
              <Select value={timeRange} onValueChange={setTimeRange}>
                <SelectTrigger className="w-40">
                  <SelectValue placeholder="Select period" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="24hours">Last 24 hours</SelectItem>
                  <SelectItem value="7days">Last 7 days</SelectItem>
                  <SelectItem value="30days">Last 30 days</SelectItem>
                  <SelectItem value="90days">Last 90 days</SelectItem>
                  <SelectItem value="1year">Last year</SelectItem>
                </SelectContent>
              </Select>
              <Button variant="outline">
                <Filter className="mr-2 h-4 w-4" />
                Filter
              </Button>
              <Button variant="outline">
                <Download className="mr-2 h-4 w-4" />
                Export
              </Button>
            </div>
          </div>

          {/* Key Metrics */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
            <MetricCard
              title="Total Revenue"
              value={formatCurrency(analytics.revenue)}
              growth={analytics.growth.revenue}
              icon={<DollarSign className="h-6 w-6" />}
            />
            <MetricCard
              title="Total Orders"
              value={analytics.orders}
              growth={analytics.growth.orders}
              icon={<ShoppingCart className="h-6 w-6" />}
            />
            <MetricCard
              title="Total Customers"
              value={analytics.customers}
              growth={analytics.growth.customers}
              icon={<Users className="h-6 w-6" />}
            />
            <MetricCard
              title="Product Views"
              value={analytics.views.toLocaleString()}
              growth={analytics.growth.views}
              icon={<Eye className="h-6 w-6" />}
            />
          </div>

          {/* Additional Metrics */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
            <MetricCard
              title="Conversion Rate"
              value={analytics.conversionRate}
              growth={2.1}
              icon={<TrendingUp className="h-6 w-6" />}
              suffix="%"
            />
            <MetricCard
              title="Avg Order Value"
              value={formatCurrency(analytics.avgOrderValue)}
              growth={-1.2}
              icon={<Package className="h-6 w-6" />}
            />
            <MetricCard
              title="Active Products"
              value={analytics.products}
              growth={5.0}
              icon={<Package className="h-6 w-6" />}
            />
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Top Products */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Top Performing Products</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {topProducts.map((product, index) => (
                    <div key={product.id} className="flex items-center justify-between p-3 border rounded-lg">
                      <div className="flex items-center gap-3">
                        <div className="h-8 w-8 bg-primary/10 rounded-full flex items-center justify-center text-sm font-medium text-primary">
                          {index + 1}
                        </div>
                        <div>
                          <p className="font-medium">{product.name}</p>
                          <p className="text-sm text-muted-foreground">
                            {product.sales} sales  {product.views.toLocaleString()} views
                          </p>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="font-medium">{formatCurrency(product.revenue)}</p>
                        <p className="text-sm text-muted-foreground">{product.conversionRate}% conv.</p>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>

            {/* Recent Orders */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Recent Orders</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {recentOrders.map((order) => (
                    <div key={order.id} className="flex items-center justify-between p-3 border rounded-lg">
                      <div className="flex items-center gap-3">
                        <div className="h-8 w-8 bg-muted rounded-full flex items-center justify-center">
                          <ShoppingCart className="h-4 w-4 text-muted-foreground" />
                        </div>
                        <div>
                          <p className="font-medium">{order.id}</p>
                          <p className="text-sm text-muted-foreground">{order.customer}</p>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="font-medium">{formatCurrency(parseInt(order.amount))}</p>
                        <div className="flex items-center gap-2">
                          <Badge variant={order.status === "Completed" ? "default" : "secondary"} className="text-xs">
                            {order.status}
                          </Badge>
                          <span className="text-xs text-muted-foreground">{order.time}</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Performance Chart Placeholder */}
          <Card className="mt-6">
            <CardHeader>
              <CardTitle className="text-lg">Revenue Overview</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="h-64 bg-muted rounded-lg flex items-center justify-center">
                <div className="text-center">
                  <TrendingUp className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
                  <p className="text-muted-foreground">Revenue chart will be displayed here</p>
                  <p className="text-sm text-muted-foreground mt-2">Integration with charting library needed</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </main>
      </div>
    </div>
  )
}
